import React from 'react';
import Layout from "../components/Layout";
import Card from "../components/Card";
import styled from 'styled-components'
import useCardVisible from "../hooks/useCardVisible";

const Page = styled.div`
  .subTitle {
    padding: 10px 20px 0;
    font-size: 14px;
  }
  p{
    font-size: 12px;
    color:#AF9999;
  }
`

export const Settings: React.FC = () => {
    const {CardVisibleList,toggleVisible}=useCardVisible()
    const onChange = (checked: boolean,pageName:string,titleName:string) => {
        toggleVisible(pageName,titleName,checked)
    }
    return (
        <Layout headerName='' navVisible={true}>
            {CardVisibleList.map(item=>{
                return <Page key={item.title}>
                    <div className="subTitle">{item.title}</div>
                    {item.Datas.map(data=>{
                        return <Card key={data.subTitle}
                                     title={data.subTitle}
                                     checked={data.visible}
                                     Tag={true}
                                     ToggleTag={checked => onChange(checked,item.title,data.subTitle)}>
                            <p>{data.content}</p>
                        </Card>
                    })}
                </Page>
            })}
        </Layout>
    )
}
