import React from 'react';
import styled from 'styled-components';
import {RecordItem} from '../../hooks/useRecords';
import useCates from '../../hooks/useCates';
import {Empty} from 'antd';

const Wrapper=styled.ul`
  max-height: 150px;
  overflow: scroll;
  &::-webkit-scrollbar{
    display: none;
  }
  li{
    padding: 12px 0;
    border-bottom: 1px solid #ddd;
    font-size: 14px;
    >div{
      display: flex;
      justify-content: space-between;
      >div{
        margin-bottom: 4px;
      }
    }
    .expense{
      font-size: 10px;
    }
  }
`
type Prop={
  records:RecordItem[]
}

const DealData:React.FC<Prop> =(props)=>{
  const {getCateList,formatExpenseName} =useCates()
  return (
    <Wrapper>
      { props.records.length?<li>
        <div className='dealTip'>
          <div className='Cate'>{getCateList(props.records[props.records.length-1].cateIds)}</div>
          <div className='spend'>{props.records[props.records.length-1].amount}</div>
        </div>
        <div className='expense'>
          <div className="expenseType">{formatExpenseName(props.records[props.records.length-1].expenseType)}</div>
          <div className="expenseAccount">{props.records[props.records.length-1].accountName}</div>
        </div>
      </li>:<Empty description={<p></p>}/>
      }
    </Wrapper>
  )
}
export default DealData
