import styled from 'styled-components';
import React, {useState} from 'react';
import {useHistory} from 'react-router-dom';
import Tip from '../../components/Tip';
import useCates from '../../hooks/useCates';
import {expenseType} from '../../hooks/useCharts';
const Wrapper = styled.div`
  margin:0 40px;
  .title{
    font-size: 12px;
    color: #AF9999;
  }
  .cateList{
     display: flex;
     flex-wrap: wrap;
     align-items: center;
     justify-content: flex-start;
     margin-top: 10px;
     li{
      width: 33.33%;
      font-size: 12px;
      text-align: center;
      margin-top: 10px;
      span{
        display: inline-block;
        border: .5px solid #ddd;
        padding: 4px 22px;
        border-radius: 6px;
        &.selected{
          border-color: red;
          color: red;
        }
      }
      &:last-child{
        color: #ccc;
      }
      
     }
  }
`;
const AllCateList=styled.div`
  width: 100%;
  padding: 0 -10px;
  .subTitle{
    background-color:#ddd;
    font-size: 12px;
    text-align: center;
    padding: 4px 0;
  }
  >ul{
    display: flex;
    flex-direction: column;
    font-size: 12px;
    li{
      border-bottom: 1px solid #ddd;
      span{
        display: inline-block;
        padding: 14px 16px;
        
      }
      &:last-child{
        border: none;
      }
    }
  }
`

type Props={
  value:number[];
  type: expenseType;
  onChange:(selected: number[])=>void
}
const Cate:React.FC<Props> =(props)=>{
  const [CateVisible,setCateVisible]=useState(false)
  const {cates,findCateIndex}=useCates()
  const selectedTagIds=props.value
  const history=useHistory()
  const onChange=(visible: boolean)=>{
    const newVisible=!visible
    setCateVisible(newVisible)
    return newVisible
  }
  const getClass=(cateId: number)=>{
    return selectedTagIds.indexOf(cateId)>=0?'selected':''
  }
  const onToggleCate=(cateId:number)=>{
    const index=selectedTagIds.indexOf(cateId)
    if(index>=0){
      props.onChange(selectedTagIds.filter(t=>t!==cateId))
    }else {
      props.onChange([...selectedTagIds,cateId])
    }
  }
  const changePosition=(id: number)=>{
    const index=findCateIndex(id)
    const data=cates[index]
    cates.splice(index,1)
    cates.splice(7,0,data)
    onToggleCate(id)
  }
  // eslint-disable-next-line
  const CalCates=()=>{
    return cates.filter(cate=>cate.expenseType=== props.type)
  }
  return (
    <Wrapper>
      <div className="title"></div>
      <ul className="cateList">
        {CalCates().slice(0,8).map(cate=>{
          return <li key={cate.id}
                     onClick={()=>{onToggleCate(cate.id)}}
          ><span className={getClass(cate.id)}>{cate.name}</span></li>
        })}
        <li onClick={()=>{onChange(CateVisible)}}><span></span></li>
      </ul>
      <Tip
        visible={CateVisible}
        titleName=''
        ButtonName=''
        onChange={CateVisible=>{onChange(CateVisible)}}
        onClick={()=>{history.push('/home/addDeal/ManageCate')}}
      >
        <AllCateList>
          <div className="subTitle"></div>
          <ul>
            {CalCates().map((cate)=>{
              return <li key={cate.id} onClick={()=>changePosition(cate.id)}><span className={getClass(cate.id)}>{cate.name}</span></li>
            })}
          </ul>
        </AllCateList>
      </Tip>
    </Wrapper>
  )
}

export default Cate
