import React, {useState} from 'react';
import Layout from '../components/Layout';
import Card from '../components/Card';
import Icon from '../components/Icon';
import useAccounts from '../hooks/useAccounts';
import AddButton from '../components/AddButton';
import styled from 'styled-components';
import {Modal} from 'antd';

const Wrapper = styled.div`
    .assets{
      padding: 20px 20px 10px 0px;
      font-size: 24px;
      color: #0AC775;
      border-bottom: .5px solid #ddd;
    }
    .total{
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin-top: 14px;
      font-size: 12px;
      li{
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
    .accountWrapper{
      display: flex;
      align-items: center;
      min-height: 40px;
      .AccountName{
        flex-grow: 1;
      }
    }
`;

export const Account: React.FC = () => {
  const {accountList, addAccount, calNetAssets} = useAccounts();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [accountName, setAccountName] = useState('');
  const [accountAmount, setAccountAmount] = useState<string>('');
  const newAssets = calNetAssets();
  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    if (accountName && accountAmount) {
      if (addAccount(accountName, Number(accountAmount))) {
        window.alert('');
      } else {
        window.alert(',');
      }
    }
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };
  const InputStyleObj = {
    width: '100%',
    border: 'none',
    borderBottom: '1px solid #ccc',
    outline: 'none',
    fontSize: '14px',
    marginTop: '10px'
  };
  return (
    <Layout headerName='' navVisible={true}>
      <Wrapper>
        <Card title=''>
          <div className="assets">{newAssets}</div>
          <ul className="total">
            <li>
              <div className="subTitle"></div>
              <div className="assetsAmount">{newAssets>=0?newAssets:0}</div>
            </li>
            <li>
              <div className="subTitle"></div>
              <div className="assetsAmount">{newAssets<0?-newAssets:0}</div>
            </li>
          </ul>
        </Card>
        {accountList.map(account => {
          return <Card key={account.id}>
            <div className='accountWrapper'>
              <Icon name='money' fill='#0AC775'></Icon>
              <div className="AccountName">{account.accountName}</div>
              <div className="AccountAmount">{account.amount}</div>
            </div>
          </Card>;
        })}
      </Wrapper>
      <AddButton onChange={showModal}></AddButton>
      <Modal title="" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
        <input placeholder=''
               type="text"
               value={accountName}
               onChange={(e) => setAccountName(e.target.value)}
               style={InputStyleObj}/>
        <input placeholder=''
               type="number"
               value={accountAmount}
               onChange={(e) => setAccountAmount(e.target.value)}
               style={InputStyleObj}/>
      </Modal>
    </Layout>
  );
};
