import React from 'react';
import Nav from './Nav';
import styled from 'styled-components';
import Header from './header';

const Wrapper = styled.div`
  max-width: 500px;
  margin: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
`;
const Main = styled.div`
  flex-grow: 1;
  overflow: auto;
  &::-webkit-scrollbar{
    display: none;
  }
  &.addDeal{
    background-color:#fff;
  }
`;
type Props={
  headerName: string,
  iconName?: string,
  back?: boolean,
  navVisible?: boolean,
  className?: string
}

const Layout:React.FC<Props> = (props: any) => {
  return (
    <Wrapper>
      <Header headerName={props.headerName}
              IconName={props.iconName}
              back={props.back}
      ></Header>
      <Main className={props.className}>
        {props.children}
      </Main>
      {props.navVisible?<Nav/>:''}
    </Wrapper>
  );
};

export default Layout;
