import {useEffect, useRef} from 'react';

const useUpdate=(fn: ()=>void,deps: any[])=>{
  const count=useRef(0)
  useEffect(()=>{
    count.current+=1
  })
  useEffect(()=>{
    // eslint-disable-next-line
    if(count.current>1){
      fn()
    }
    // eslint-disable-next-line
  },[fn,...deps])  //
}

export {useUpdate}

