import {useEffect, useState} from 'react';
import {defaultCateList} from '../lib/defaultCateList';
import {Id} from '../lib/Id';
import {Cate} from '../lib/defaultCateList';
import {useUpdate} from './useUpdate';
import {RecordItem} from './useRecords';
import {expenseType} from './useCharts';

const hashTable={
  '-':'',
  '+':''
}
const useCates=()=>{
  const [cates,setCates]=useState<Cate[]>([])
  useEffect(()=>{
    let localCates=JSON.parse(window.localStorage.getItem('cates')||'[]')
    if(localCates.length===0){
      localCates=defaultCateList
    }
    setCates(localCates)
  },[])
  useUpdate(() => {
    window.localStorage.setItem('cates', JSON.stringify(cates));
  }, [cates]);
  const findCate=(id:number)=>cates.filter(cate=>cate.id===id)[0]
  const findCateIndex=(id: number)=>{
    const localCates:Cate[]=JSON.parse(window.localStorage.getItem('cates')||'[]')
    const obj=localCates.find(cate=>cate.id===id)
    if(obj){
      return localCates.indexOf(obj)
    }else {
      window.alert('')
      return -1
    }
  }
  const deleteCate=(id: number)=>{
    setCates(cates.filter(cate=>cate.id!==id))
  }
  const addCate=(cateName: string,expenseType:expenseType)=>{
    if(cateName!==null && cateName!==''){
      if(getId(cateName)===-1){
        setCates([...cates,{id:(new Id().value),name:cateName,expenseType:expenseType,recordList:[]}])
      }else {
       window.alert('')
      }
    }
  }
  const getName=(id:number)=>{
    const cate=cates.filter(t=>t.id===id)[0]
    return cate?cate.name:''
  }
  const getId=(cateName: string)=>{
    let res=-1
    for(let i=0;i<cates.length;i++){
      if(cates[i].name===cateName){
        res=cates[i].id
        break
      }
    }
    return res
  }
  const getCate=(ExpenseType:expenseType)=>cates.filter(cate=>cate.expenseType===ExpenseType)
  const getCateList=(cateIds: number[])=>{
    const arr=[] as string[]
    cateIds.forEach(cateId=>{
      arr.push(getName(cateId))
    })
    return arr.join(',')
  }
  const formatExpenseName=(expenseType:expenseType)=>{
    let res=''
    if(expenseType){
      res=hashTable[expenseType]
    }
    return res
  }
  const updateCate=(record: RecordItem)=>{
    record.cateIds.forEach(id=>{
      const index=findCateIndex(id)
      if(cates[index].recordList!==undefined){
        cates[index].recordList!.push(record)
      }else {
        cates[index].recordList=[]
        cates[index].recordList!.push(record)
      }
    })
    const newCates=cates
    setCates(newCates)
    window.localStorage.setItem('cates', JSON.stringify(cates));
  }
  const getRecordList=(id: number)=>{
    const localCates=JSON.parse(window.localStorage.getItem('cates')||'[]')
    const index=findCateIndex(id)
    if(localCates[index].recordList===undefined){
      return []
    }else {
      return localCates[index].recordList
    }
  }


  return {
    cates,
    setCates,
    findCate,
    findCateIndex,
    deleteCate,
    addCate,
    getName,
    getCate,
    getId,
    getCateList,
    formatExpenseName,
    updateCate,
    getRecordList
  }
}

export default useCates
