import React from 'react';
import styled from 'styled-components';
import useBudget from '../../hooks/useBudget';
import useRecords from '../../hooks/useRecords';

const Wrapper=styled.div`
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 20px 0;
  >.payment{
    display: flex;
    flex-direction: column;
    align-items: center;
    span{
      font-size: 12px;
      color: #CAC1C1;
    }
    div{
      font-size: 14px;
      margin-top: 2px;
    }
  }
  >.circleButton{
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-color:#F6F0F0;
    display: flex;
    justify-content: center;
    align-items: center;
    >.circleInner{
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background-color:#fff;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      h3,.budgetAmount{
        color: #CAC1C1;
        font-size: 12px;
        margin-bottom: 0;
      }
      .add{
        color: #000;
        font-size: 16px;
        margin: 4px 0;
      }
    }
  }
`
const Budget:React.FC=()=>{
  const {addBudget,budget,getRestBudget}=useBudget()
  const {calPayOrIn}=useRecords()
  const paymentAmount=calPayOrIn('-')
  const incomeAmount=calPayOrIn('+')
  const onAddBudget=()=>{
    addBudget()
  }
  const restAmount=()=>{
    return Number(incomeAmount)-Number(paymentAmount)
  }
  return (
    <Wrapper>
      <div className="payment">
        <span></span>
        <div className="payAmount">{restAmount()}</div>
      </div>
      <div className="circleButton">
        <div className="circleInner">
          <h3>{getRestBudget(-restAmount())}</h3>
          <div className='add' onClick={onAddBudget}></div>
          <div className="budgetAmount">{budget}</div>
        </div>
      </div>
    </Wrapper>
  )
}

export default Budget
