import {useEffect, useState} from 'react';
import {Id} from '../lib/Id';
import {account} from '../lib/defaultAccountList';
import {useUpdate} from './useUpdate';
import {expenseType} from './useCharts';


const useAccounts=()=>{
  let [accountList,setAccountList]=useState<account[]>([])
  //useEffectaccountListaccountList
  useEffect(()=>{
    setAccountList(JSON.parse(window.localStorage.getItem('accountList')||'[]'))
  },[])
  useUpdate(() => {
    window.localStorage.setItem('accountList', JSON.stringify(accountList));
  }, [accountList]);

  const addAccount=(name:string,amount:number)=>{
    if(name!==''&&name!==null&&amount>=0){
      if(accountList.filter(account=>account.accountName===name).length>0){
        window.alert('')
        return false
      }else {
        const newAccount={id:(new Id().value),
          accountName:name,
          amount:amount}
        setAccountList([...accountList,newAccount])
        return true
      }
    }
  }
  const calNetAssets=()=>{
    let sum=0
    for(let i=0;i<accountList.length;i++){
      sum+=accountList[i].amount
    }
    return sum
  }
  const updateAccount=(accountName: string,amount: number,expenseType: expenseType)=>{
    const newAccountList=accountList.map(account=>{
      if(account.accountName===accountName){
        if(expenseType==='-'){
          if(account.amount<amount){
            window.alert('')
          }
          account.amount-=amount
        }else if(expenseType==='+'){
          account.amount+=amount
        }
      }
      return account
    })
    setAccountList(newAccountList)
  }
  const getAccountAmount=(accountName: string)=>{
    return accountList.filter(account=>account.accountName===accountName)[0].amount
  }
  return {
    accountList,
    setAccountList,
    addAccount,
    calNetAssets,
    updateAccount,
    getAccountAmount
  }
}

export default useAccounts
