import Card from '../../components/Card';
import React, {useState} from 'react';
import Tip from '../../components/Tip';
import styled from 'styled-components';

const ContentWrapper=styled.ul`
  display: flex;
  flex-direction: column;
  >li{
    font-size: 14px;
    border-bottom: .5px solid #ddd;
    padding: 10px 20px;
  }
`



type Prop={
  titleName:string,
  SwitchName:string,
  onChange:(SwitchName: string)=>void
}

const CommonCensus:React.FC<Prop> =(props)=>{
  const [visibleTip,setVisibleTip]=useState(false)
  const categoryMap = {'-': '', '+': ''};
  const [categoryList] = useState<('+' | '-')[]>(['-', '+']);
  const toggleTip=
    (visible: boolean)=>{
      const newVisible=!visible
      setVisibleTip(newVisible)
      return newVisible
    }
  return (
    <div>
      <Card title={props.titleName}
            switch={true}
            onChange={()=>toggleTip(visibleTip)}
            SwitchName={props.SwitchName}
      >
        {props.children}
      </Card>
      <Tip onChange={visibleTip1=>toggleTip(visibleTip1)} visible={visibleTip} titleName=''>
        <ContentWrapper>
          {categoryList.map(c=><li key={c} onClick={()=>{props.onChange(categoryMap[c])}}>
            {categoryMap[c]}
          </li>)}
        </ContentWrapper>
      </Tip>
    </div>
  )
}

export default CommonCensus
