import Card from '../components/Card';
import Layout from '../components/Layout';
import React from 'react';
import styled from 'styled-components';
import {RecordItem} from '../hooks/useRecords';
import useCates from '../hooks/useCates';
import dayjs from 'dayjs';
import {Empty} from 'antd';
import {expenseType} from '../hooks/useCharts';

const Wrapper=styled.div`
  .title{
    padding: 0 20px;
    margin-top: 15px;
    color: #c1c1c1;
    font-size: 14px;
  }
  .totalData{
    >li{
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: .5px solid #ddd;
      padding: 14px 0;
      &:first-child{
        padding-top: 0;
      }
      &:last-child{
        border-bottom: 0;
        padding-bottom: 0;
      }
    }
  }
  .recordItems{
    >li{
      display: flex;
      flex-direction: column;
      border-bottom: .5px solid #ddd;
      padding: 10px 0;
      &:first-child{
        padding-top: 0;
      }
      &:last-child{
        padding-bottom: 0;
        border-bottom: 0;
      }
      >div{
        display: flex;
        justify-content: space-between;
        align-items: center;
        
      }
      .expenseAndAccount{
           font-size: 12px;
      }
    }
  }
`

type Prop={
  records: RecordItem[],
  headerName:string
}
const mapTable:{[k:number]:string} ={
  1:'',
  2:'',
  3:'',
  4:'',
  5:'',
  6:'',
  7:''
}
const DealList:React.FC<Prop> =(props)=>{
  const {getCateList,formatExpenseName}=useCates()
  const hash: { [K: string]: RecordItem[] } = {};
  const formatKey=(date:string)=>{
    const day = dayjs(date);
    const now = dayjs();
    if(day.isSame(new Date(),'day')){
      return ''
    }else if(day.isSame(now.subtract(1,'day'),'day')){
      return ''
    }else if(day.isSame(now.subtract(2,'day'),'day')){
      return ''
    }else if(day.isSame(now,'year')){
      return day.format('MDD')+' '+mapTable[day.day()]
    }else if(day.isSame(now,'month') && (day.isSame(now,'year'))){
      return day.format('DD')+' '+mapTable[day.day()]
    }else {
      return day.format('YYYYMMDD')+' '+mapTable[day.day()];
    }
  }
  props.records.forEach(r=>{
    const key=dayjs(r.createdAt).format('YYYY-MM-DD')
    const value=r;
    if(!(key in hash)){
      hash[key]=[]
    }
    hash[key].push(value)
  })
  const array = Object.entries(hash).sort((a, b) => {
    if (a[0] === b[0]) return 0;
    if (a[0] > b[0]) return -1;
    if (a[0] < b[0]) return 1;
    return 0;
  });
  array.forEach(arr=>{
    arr[0]=formatKey(arr[0])
  })
  const calRest=()=>{
    return calAmount('+')-calAmount('-')
  }
  const calAmount=(type: expenseType)=>{
    const IncomeList= props.records.filter(record=>record.expenseType===type)
    let sum=0
    IncomeList.forEach(item=>{
      sum+=Number(item.amount)
    })
    return sum
  }
  return (
    <Layout headerName={props.headerName} back={true}>
      <Wrapper>
        <div className="item">
          <div className="title">{props.records.length}</div>
          <Card>
            <ul className='totalData'>
              <li>
                <span></span><span>{calAmount('+')}</span>
              </li>
              <li>
                <span></span><span>{-calAmount('-')}</span>
              </li>
              <li>
                <span></span><span>{calRest()}</span>
              </li>
            </ul>
          </Card>
        </div>
        {array.length?array.map(([date,records])=>{
          return <div className="item" key={date}>
            <div className="title">{date}</div>
            <Card>
              <ul className='recordItems'>
                {records.map(r=>{
                  return  <li key={r.id}>
                    <div className="nameAndAmount">
                      <div className="cateName">{getCateList(r.cateIds)}</div>
                      <div className="amount">{r.amount}</div>
                    </div>
                    <div className="expenseAndAccount">
                      <div className="expense">{formatExpenseName(r.expenseType)}</div>
                      <div className="account">{r.accountName}</div>
                    </div>
                  </li>
                })}
              </ul>
            </Card>
          </div>
        }):<Empty/>}

      </Wrapper>

    </Layout>
  )
}
export default DealList
