import React from 'react'
import {ReactEcharts} from '../../components/Chart';
import CommonCensus from './CommonCensus';
import styled from 'styled-components';

const TotalGreen=styled.div`
  margin-top: 10px;
  font-size: 18px;
  color: #0AC775;
`
const TotalRed=styled.div`
  margin-top: 10px;
  font-size: 18px;
  color: #F17A73;
`

type Prop={
  SwitchName:string,
  onChange:(SwitchName: string)=>void,
  totalPayment:string,
  totalIncome:string,
  OptionIn:object,
  OptionOut:object,
  titleName:string
}
const StatisticChart:React.FC<Prop> =(props)=>{
  const {titleName,SwitchName,onChange,totalIncome,totalPayment,OptionIn,OptionOut}=props
  return (
    <CommonCensus titleName={titleName}
                  SwitchName={SwitchName}
                  onChange={onChange}
    >
      {SwitchName===''?
        <div>
          <TotalRed>-{totalPayment}</TotalRed>
          <ReactEcharts option={OptionOut}/>
        </div>:
        <div>
          <TotalGreen>{totalIncome}</TotalGreen>
          <ReactEcharts option={OptionIn}/>
        </div>}
    </CommonCensus>
  )
};

export default StatisticChart
