import {useEffect, useState} from 'react';
import {useUpdate} from './useUpdate';
import {Id} from '../lib/Id';
import {expenseType} from './useCharts';

export type RecordItem={
  id:number,
  expenseType:expenseType,
  amount:string,
  accountName:string,
  createdAt:string,  //ISO 8601
  cateIds:number[],
  remark:string
}
export type newRecordItem = Omit<RecordItem, 'id'>

const useRecords=()=>{
  const [records,setRecords]=useState<RecordItem[]>([])
  useEffect(()=>{
    setRecords(JSON.parse(window.localStorage.getItem('records') || '[]'));
  },[])
  useUpdate(()=>{
    window.localStorage.setItem('records', JSON.stringify(records));
  },[records])
  const addRecord=(newRecord:newRecordItem)=>{
    if (Number(newRecord.amount) <= 0) {
      alert('');
      return false;
    }
    if (newRecord.cateIds.length === 0) {
      alert('');
      return false;
    }
    if(newRecord.accountName===''){
      alert('')
      return false
    }
    let newCreateAt=''
    if(newRecord.createdAt===''){
      newCreateAt=(new Date()).toISOString()
    }else {
      newCreateAt=newRecord.createdAt
    }
    const record={...newRecord,createdAt:newCreateAt,id:(new Id().value)}
    setRecords([...records,record])
    return true
  }
  //
  const calPayOrIn=(expenseType: expenseType)=>{
    const array= records.filter(record=>record.expenseType===expenseType)
    let sum=0
    array.forEach(item=>{
      sum+=Number(item.amount)
    })
    return sum.toString()
  }
  return {records,addRecord,calPayOrIn}
}

export default useRecords
