import React from 'react';
import styled from 'styled-components';
import {DatePicker} from 'antd';
import moment from 'moment'

const Wrapper = styled.div`
    margin: 10px 40px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
    .subTitle{
     color: #AF9999;
     font-size: 8px;
    }
    .info{
      font-size: 12px;
      color: #000;
      input{
        border: none;
        outline: none;
        color: #828482;
      }
      .ant-picker{
        width: 100%;
        padding: 0;
      }
    }
`;

type Prop={
  value:string,
  onHandle:(createdAt: string)=>void
}

const AddDate: React.FC<Prop> = (props) => {
  function onChange(date: Date, dateString: string) {
    if(dateString){
      props.onHandle(dateString)
    }
  }

  return (
    <Wrapper>
      <div className="subTitle"></div>
      <div className="info">
        {/*@ts-ignore*/}
        <DatePicker onChange={onChange}
                    bordered={false}
                    inputReadOnly
                    defaultValue={moment(props.value.split('T')[0],'YYYY-MM-DD')}
                    placeholder=''
        >
        </DatePicker>
      </div>
    </Wrapper>
  );
};

export default AddDate;
