import React, {useState} from 'react';
import {useParams,useHistory} from 'react-router-dom';
import useCates from '../../hooks/useCates';
import SaveButton from "../../components/SaveButton";
import DealList from '../DealList';
import {Modal} from 'antd';
type Params = {
  id: string
}
const CateDetail: React.FC=()=>{
  const {getName,deleteCate,getRecordList} = useCates();
  const history=useHistory()
  let {id: idString} = useParams<Params>();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const records=getRecordList(Number(idString))||[]
  const handleOk = () => {
    if(records.length>0){
      window.alert('')
      setIsModalVisible(false);
      return
    }else {
      deleteCate(Number(idString))
      window.alert('')
      history.goBack()
    }
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };


  const getHeaderName=()=>{
    const Name=getName(Number(idString))
    return Name ? Name : '';
  }
  const DelCate=()=>{
    setIsModalVisible(true);
  }
  return (
    <div>
      <DealList records={records} headerName={getHeaderName()}></DealList>
      <SaveButton buttonName='' onClick={DelCate}>
      </SaveButton>
      <Modal title="" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
        <p></p>
      </Modal>
    </div>
  )
}

export default CateDetail
