import React from 'react';
import styled from 'styled-components';
import {account} from '../../lib/defaultAccountList';
import {Empty} from 'antd';

const Wrapper=styled.ul`
  display: flex;
  flex-direction: column;
  max-height: 300px;
  overflow: scroll;
  &::-webkit-scrollbar{
    display: none;
  }
  >li{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
    font-size: 14px;
    >.amount{
      color: #59B91E;
    }
  }
  .subTitle{
    border: none;
    color: #CAC1C1;
    font-size: 12px;
  }
`

type Prop={
  accountList: account[]
}

const AccountData:React.FC<Prop> =(props)=>{
  return (
    <Wrapper>
      <li className='subTitle'>
        <span className='account'></span>
      </li>
      {props.accountList.length?
        props.accountList.map(account=>{
          return <li key={account.id}>
            <span className='accountName'>{account.accountName}</span>
            <span className='amount'>{account.amount}</span>
          </li>
        }):<Empty description={
          <p></p>
        }/>
      }
    </Wrapper>
  )
}
export default AccountData
