import React from 'react';
import styled from 'styled-components';
import {Switch} from 'antd'

const CardWrapper = styled.div`
  min-height: 70px;
  padding: 14px;
  background-color: #fff;
  border-radius: 4px;
  margin: 14px;
  display: flex;
  flex-direction: column;

  .CardTitle {
    display: flex;
    justify-content: space-between;
    align-items: center;

    > header {
      font-size: 14px;
    }

    > span {
      font-size: 12px;
      color: #6F837A;
      border-bottom: .5px solid #6F837A;
    }
    .ant-switch-checked{
      background-color: #0AC775;
    }
  }

`
const Main = styled.div`
  flex-grow: 1;
  overflow: auto;
  padding: 5px 0;
`;

type Props = {
    title?: string,
    switch?: boolean,
    onChange?: () => void,
    SwitchName?: string,
    Tag?: boolean,
    ToggleTag?:(checked: boolean)=>void,
    checked?:boolean
} & React.ScriptHTMLAttributes<HTMLDivElement>
const Card: React.FC<Props> = (props) => {
    return (
        <CardWrapper>
            <div className='CardTitle'>
                {props.title ? <header>{props.title}</header> : ''}
                {props.SwitchName && props.switch ? <span onClick={props.onChange}>{props.SwitchName}</span> : ''}
                {/*@ts-ignore*/}
                {props.Tag && props.ToggleTag  ? <Switch defaultChecked  checked={props.checked} onChange={checked=> props.ToggleTag(checked)}></Switch>:''}
            </div>
            <Main className={props.className}>
                {props.children}
            </Main>
        </CardWrapper>
    )
}

export default Card
