import {useState,useEffect} from 'react'
import {SettingData,CardVisble} from "../lib/SettingData";
import _ from 'lodash'
import {useUpdate} from "./useUpdate";

const useCardVisible=()=>{
    const [CardVisibleList,setCardVisibleList]=useState<CardVisble[]>(SettingData)
    useEffect(()=>{
        let localList=JSON.parse(window.localStorage.getItem('CardVisibleList')||'[]')
        if(localList.length===0){
            window.localStorage.setItem('CardVisibleList', JSON.stringify(CardVisibleList));
            localList=SettingData
        }
        setCardVisibleList(localList)
    },[]) // eslint-disable-line react-hooks/exhaustive-deps
    useUpdate(() => {
        window.localStorage.setItem('CardVisibleList', JSON.stringify(CardVisibleList));
    }, [CardVisibleList]);
    const  getHomeData=()=>{
        return  CardVisibleList[0].Datas
    }
    const getStatisticData=()=>{
        return CardVisibleList[1].Datas
    }
    const findIndex=(Title: string,SubTitle:string)=>{
        let res=[] as number[]
        for(let i=0;i<CardVisibleList.length;i++){
            if(CardVisibleList[i].title===Title){
                for(let j=0;j<CardVisibleList[i].Datas.length;j++){
                    if(CardVisibleList[i].Datas[j].subTitle===SubTitle){
                        res=[i,j];
                        break
                    }
                }
            }
        }
        return res
    }
    const toggleVisible=(pageName:string,cardName: string,visible:boolean)=>{
        const index=findIndex(pageName,cardName)
        if(index.length){
            const newList=_.cloneDeep(CardVisibleList)
            newList[index[0]].Datas[index[1]].visible=visible
            console.log(newList)
            setCardVisibleList(newList)
        }
    }


    return {
        CardVisibleList,
        setCardVisibleList,
        toggleVisible,
        getHomeData,
        getStatisticData
    }
}
export default useCardVisible