import React from 'react';
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect
} from 'react-router-dom';
import {Account} from './views/Account';
import {Home} from './views/Home';
import {Money} from './views/money';
import {Settings} from './views/Settings';
import {Statistics} from './views/Statistics';
import {User} from './views/User';
import {NotFound} from './views/NotFound';
import AddDeal from './views/AddDeal';
import ManageCate from './views/AddDeal/ManageCate';
import CateDetail from './views/AddDeal/CateDetail';
import AllDeal from './views/AllDeal';
import UserInfo from "./views/User/userInfo";
function App() {
  return (
    <Router>
      <Switch>
        <Route path="/home" exact>
          <Home/>
        </Route>
        <Route path="/home/allDeal" exact>
          <AllDeal/>
        </Route>
        <Route path="/home/addDeal" exact>
          <AddDeal/>
        </Route>
        <Route path="/home/addDeal/ManageCate" exact>
          <ManageCate/>
        </Route>
        <Route path="/home/addDeal/ManageCate/:id" exact>
          <CateDetail/>
        </Route>
        <Route path="/money" exact>
          <Money/>
        </Route>
        <Route path="/account" exact>
          <Account/>
        </Route>
        <Route path="/settings" exact>
          <Settings/>
        </Route>
        <Route path="/statistics" exact>
          <Statistics/>
        </Route>
        <Route path="/user" exact>
          <User/>
        </Route>
        <Route path="/user/userInfo" exact>
          <UserInfo/>
        </Route>
        <Redirect from="/" to="/home"/>
        <Route path="*">
          <NotFound/>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
