import React from 'react'
import {useEffect, useRef} from 'react';
import * as echarts from 'echarts'


type Prop={
  option:Object
}& React.ScriptHTMLAttributes<HTMLDivElement>

export const ReactEcharts:React.FC<Prop> =(props)=>{
  const container=useRef(null)
  const {option}=props
  const chart=useRef(null)
  useEffect(()=>{
    // @ts-ignore
    chart.current=echarts.init(container.current,'white')
  },[])
  useEffect(()=>{
    // @ts-ignore
    chart.current.setOption(option)
  },[option])
  const style={
    width:"100%",
    height:"200px",
    display:"flex",
    justifyContent:"center",
    alignItems:"center"
  }
  return (
    <div ref={container} style={style}></div>
  )
}
