import styled from 'styled-components';
import React from 'react';
const Wrapper = styled.ul`
  display: flex;
  min-height: 90px;
  justify-content: space-around;
  align-items: center;
  margin-bottom: -10px;
  overflow: hidden;
  li{
    display: flex;
    flex-direction: column;
    font-size: 14px;
    align-items: center;
    >div{
      margin-top: 8px;
      color: #59B91E;
      font-size: 16px;
      &.redStyle{
        color: #F17A73;
      }
      &.greenStyle{
        color: #59B91E;
      }
    }
    .payment{
      color: #F17A73;
    }
   
  }
`;

type Prop={
  value: {
    income: string,
    payment: string
  }
}
const DataList:React.FC<Prop> =(props)=>{
  const getClass=()=>Number(props.value.income) - Number(props.value.payment) < 0 ? 'redStyle' : 'greenStyle';

  return (
    <Wrapper>
      <li>
        <span></span>
        <div>{props.value.income}</div>
      </li>
      <li>
        <span></span>
        <div className='payment'>{props.value.payment}</div>
      </li>
      <li>
        <span></span>
        <div className={getClass()}>{Number(props.value.income)-Number(props.value.payment)}</div>
      </li>
    </Wrapper>
  )
}

export default DataList
