import styled from 'styled-components';
import {NavLink} from 'react-router-dom';
import Icon from './Icon';
import React from 'react';

const Wrapper = styled.div`
  width: 44px;
  height: 44px;
  position: absolute;
  bottom: 80px;
  right: 20px;
  border-radius: 50%;
  background-color:#0AC775;
  box-shadow: 0 6px 4px rgba(0,0,0,.1);
  display: flex;
  justify-content: center;
  align-items: center;
  .icon{
    fill: #fff!important;
  }
  a{
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;

type Prop={
  routeName?: string,
  onChange?: ()=>void
}& React.ScriptHTMLAttributes<HTMLButtonElement>

const AddButton: React.FC<Prop> = (props) => {
  return (
    <Wrapper onClick={props.onChange}>
      {props.routeName?<NavLink to={props.routeName}>
        <Icon name='add'></Icon>
      </NavLink>:<Icon name='add'></Icon>}
    </Wrapper>
  );
};

export default AddButton
