import {Cate} from '../lib/defaultCateList';
import _ from 'lodash';
import {RecordItem} from './useRecords';
import day from 'dayjs'

export type CateCharData={
  name:string,
  value:number
}

const defaultCateOption={
  tooltip: {
    trigger: 'item'
  },
  series: [
    {
      name: '',
      type: 'pie',
      radius: ['45%','70%'],
      center:['50%','50%'],
      labelLine:{
        show:true
      },
      label: {
        formatter:'{b|{b}}\n  {per|{d}%}',
        rich:{}
      },
      data: [
        {value: 100, name: ''}
      ]
    }
  ]
};
const defaultLineOption={
  tooltip: {
    trigger: 'axis'
  },
  grid: {
    left: '20px',
    right: '30px',
    bottom: '20px',
    top:'20px',
    containLabel: true
  },
  xAxis: {
    type: 'category',
    data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
    axisLabel:{
      formatter:function (value: string,index: number){
        return value.substring(5)
      }
    }
  },
  yAxis: {
    type: 'value'
  },
  series: [{
    name:'',
    data: [100],
    type: 'line',
    smooth: 0.6,
    symbol:'none',
    lineStyle: {
      color: '#5470C6',
      width: 1.5
    }
  }]
}
const hash={
  'day': {
    num:29,
    formatType:'YYYY-MM-DD',
    labelName:''
  },
  'month': {
    num:11,
    formatType:'MM',
    labelName:''
  }
}
type LineChartType='day'|'month'
export type expenseType='+'|'-'

const useCharts=()=>{
  const getCateChartData=(expenseType:expenseType)=>{
    const cates=JSON.parse(window.localStorage.getItem('cates')||'[]') as Cate[]
    const newCates=cates.filter(cate=>cate.expenseType===expenseType)
    const arr=[] as CateCharData[]
    newCates.forEach(cate=>{
      let obj={} as CateCharData
      let sum=0
      cate.recordList.forEach(item=>{
        sum+=Number(item.amount)
      })
      obj.value=sum
      obj.name=cate.name
      arr.push(obj)
    })
    const newArr=arr.filter(item=>item.value!==0)
    //
    if(newArr.length===0){
      newArr.push({value: 0, name: ''})
    }
    defaultCateOption.series[0].data=newArr
    //newOptiondefaultOption
    const newOption=_.cloneDeep(defaultCateOption)
    return newOption
  }
  const DealRenderRecord=(today: Date,RenderRecord:RecordItem[],type:LineChartType)=>{
    const array=[]
    for(let i=0;i<=hash[type].num;i++){
      let sum=0
      const dateString=day(today).subtract(i, type).format(hash[type].formatType)
      RenderRecord.forEach(record=>{
        if(record.createdAt===dateString){
          sum+=Number(record.amount)
        }
      })
      const found=_.find(RenderRecord,{createdAt: dateString})
      array.push({date:dateString+hash[type].labelName,value:found?sum:0})
    }
    return array
  }
  const formatRecord=(expenseType:expenseType,records:RecordItem[],formatType:LineChartType)=>{
    const today=new Date()
    let array: any[]=[]
    const RenderRecord=records.filter(item=>item.expenseType===expenseType)
    RenderRecord.forEach(record=>{
      record.createdAt=record.createdAt.split('T')[0]
    })
    if(formatType==='day'){
      const arr=DealRenderRecord(today,RenderRecord,'day')
      array=[...arr]
    }else if(formatType==='month'){
      RenderRecord.forEach(record=>{
        record.createdAt=record.createdAt.split('-')[1]
      })
      const arr=DealRenderRecord(today,RenderRecord,'month')
      array=[...arr]
    }
    array.reverse()
    return array
  }
  const getLineChartData=(expenseType:expenseType,chartType:LineChartType)=>{
    const records=JSON.parse(window.localStorage.getItem('records')||'[]') as RecordItem[]
    const incomeData=formatRecord('+',records,chartType)
    const outData=formatRecord('-',records,chartType)
    const keys=incomeData.map(item=>item.date)
    const valueIncome=incomeData.map(item=>item.value)
    const valuePayment=outData.map(item=>item.value)
    if(keys){
      // @ts-ignore
      defaultLineOption.xAxis.data=keys
    }
    if(chartType==='month'){
      // @ts-ignore
      defaultLineOption.xAxis.axisLabel={}
    }
    let newTrendOption={}
    if(expenseType==='-'){
      defaultLineOption.series[0].data=valuePayment
      defaultLineOption.series[0].name=''
      newTrendOption=_.cloneDeep(defaultLineOption)
    }else {
      defaultLineOption.series[0].data=valueIncome
      defaultLineOption.series[0].name=''
      newTrendOption=_.cloneDeep(defaultLineOption)
    }
    return newTrendOption
  }
  return {
    getCateChartData,
    getLineChartData
  }
}

export default useCharts
