import React, {useState} from 'react';
import Tags from './Tags';
import Layout from '../../components/Layout';
import Icon from '../../components/Icon';
import useCates from '../../hooks/useCates';
import AddButton from '../../components/AddButton';
import {Link} from 'react-router-dom';
import {Modal} from 'antd';
import styled from 'styled-components';
import {expenseType} from '../../hooks/useCharts';

const CateListWrapper=styled.div`
  width: 100%;
  padding:20px;
  ul{
    display: flex;
    flex-direction: column;
    li{
      display: flex;
      align-items: center;
      margin-top: 30px;
      .icon{
        width: 40px;
        height: 40px;
      }
      .cateName{
        margin-left: 20px;
      }
    }
  }
`

const ModalInput=styled.input`
  width: 100%;
  border: none;
  outline: none;
  border-bottom: 1px solid skyblue;
  font-size: 20px;
  &::-webkit-input-placeholder{
    color: #ccc;
    font-size: 14px;
  }
`
const ManageCate:React.FC=()=>{
  const {getCate,addCate}=useCates()
  const [CateName,setCateName]=useState('')
  const [ExpenseType,setExpenseType]=useState<expenseType>('-')
  const [isModalVisible, setIsModalVisible] = useState(false);
  const showModal = () => {
    setIsModalVisible(true);
  };
  const handleOk = () => {
    console.log(CateName);
    if(CateName.length>5){
      window.alert('')
      return
    }else {
      addCate(CateName,ExpenseType)
    }
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };
  return (
    <Layout back={true} headerName='' navVisible={false} className='addDeal'>
      <Tags value={ExpenseType} onChange={(expenseType)=>{setExpenseType(expenseType)}}></Tags>
      <CateListWrapper>
        <ul>
          {
            getCate(ExpenseType).map(cate=>{
              return <li key={cate.id}>
                <Link to={`/home/addDeal/ManageCate/`+cate.id}>
                  <Icon name='file'></Icon>
                </Link>
                <div className="cateName">{cate.name}</div>
              </li>
            })
          }
        </ul>
      </CateListWrapper>
      <AddButton onChange={showModal}></AddButton>
      <Modal title="" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
        <ModalInput placeholder='5'
                    value={CateName}
                    onChange={(e)=>setCateName(e.target.value)}
        ></ModalInput>
      </Modal>
    </Layout>
  )
}

export default ManageCate
