import React from 'react';
import Layout from "../components/Layout";
import Card from "../components/Card";
import styled from 'styled-components'
import avatarSmall from 'assets/images/avatar.jpg'
import {useHistory} from 'react-router-dom'
import useUserInfo from "../hooks/useUserInfo";
const Wrapper = styled.div`
  display: flex;
  align-items: center;

  .imgWrapper {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .userName {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    margin-left: 10px;

    button {
      border: none;
      background-color: #fff;
      font-size: 10px;
      margin-top: 5px;
      color: rgb(111, 131, 122);
    }
  }
`

export const User: React.FC = () => {
    const {userInfo}=useUserInfo()
    const history=useHistory()
    const goInfo=()=>{
        history.push('/user/userInfo')
    }
    return (
        <Layout headerName='' navVisible={true}>
            <Card>
                <Wrapper>
                    <div className="imgWrapper">
                        <img src={avatarSmall} alt=""/>
                    </div>
                    <div className="userName">
                        {userInfo.userName?<span>{userInfo.userName}</span>:<span></span>}
                        <button className='editInfo' onClick={goInfo}></button>
                    </div>
                </Wrapper>
            </Card>
        </Layout>
    )
}
