import React from 'react';
import styled from 'styled-components';
const Mask=styled.div`
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color:rgba(0,0,0,.3);
  z-index: 2;
  .card{
    width: 80%;
    position: absolute;
    left: 50%;
    top: 50%;
    padding-bottom: 20px;
    transform: translate(-50%,-50%);
    min-height: 100px;
    max-height: 80vh;
    border-radius: 4px;
    background-color:#fff;
    display: flex;
    flex-direction: column;
    >.CardTitle{
      font-size: 16px;
      padding: 14px;
    }
    .content{
      flex-grow: 1;
      margin-top: 10px;
      overflow: scroll;
      &::-webkit-scrollbar{
        display: none;
      }
    }
    >button{
       color: #32B4FD;
       background-color:#fff;
       width: 100%;
       border: none;
       padding: 20px;
       font-size: 14px;
       border-radius: 4px;
    }
  }
`

type Prop={
  visible:boolean
  titleName: string,
  ButtonName?:string,
  onChange:(visible: boolean)=>void,
  onClick?:()=>void
}
const Tip:React.FC<Prop>=(props)=>{
  return (
    <div>
      {props.visible?<Mask onClick={()=>props.onChange(props.visible)}>
        <div className='card'>
          <div className="CardTitle">{props.titleName}</div>
          <div className="content">
            {props.children}
          </div>
          {props.ButtonName?<button onClick={props.onClick}>{props.ButtonName}</button>:''}
        </div>
      </Mask>:''}
    </div>
  )
}

export default Tip
