import React,{useState,useEffect} from 'react'
import styled from 'styled-components'
import Layout from "../../components/Layout";
import avatarSmall from 'assets/images/avatar.jpg'
import Input from "../../components/Input";
import {DatePicker} from 'antd'
import SaveButton from "../../components/SaveButton";
import useUserInfo, {User} from "../../hooks/useUserInfo";
import Tip from "../../components/Tip";
import moment from 'moment'
import day from 'dayjs'

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;

  .imgWrapper {
    margin-top: 20px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
    }
  }
  .sexList{
    padding: 0 20px;
    li{
      border-bottom: .5px solid #ddd;
      padding: 10px 0;
      &:last-child{
        border-bottom: none;
        padding-bottom: 0;
      }
    }
  }
  form {
    width: 100%;
    padding: 20px 40px;

    .item {
      margin: 14px 0;
      .title {
        font-size: 12px;
        color: #AF9999;
      }
      .ant-picker{
        width: 100%;
        padding: 10px 0;
        border-bottom: .5px solid #ddd!important;
      }
      .sex{
        font-size: 14px;
        padding: 10px 0;
        border-bottom: .5px solid #ddd!important;
      }
    }
  }
`
const UserInfo: React.FC = () => {
    const {setUserInfo}=useUserInfo()
    const userInfo=JSON.parse(window.localStorage.getItem('userInfo')||'{}')
    const [user,setUser]=useState<User>(userInfo)
    const [tipVisible,setTipVisible]=useState(false)
    useEffect(()=>{
        setUser(userInfo)
        console.log(user)
    },[]) // eslint-disable-line react-hooks/exhaustive-deps
    const hashMap={
        'male':'',
        'female':''
    }
    const onChange=(obj: Partial<User>)=>{
        setUser({...user,...obj})
    }
    function onChooseDate(date: Date, dateString: string){
        const newDateString=day(date).format('YYYYMMDD')
        if(newDateString){
            onChange({birth:newDateString})
        }
    }
    const ToggleVisible=(visible: boolean)=>{
        const newVisible=!visible
        setTipVisible(newVisible)
    }
    const showTip=()=>{
        setTipVisible(true)
    }
    const submit=()=>{
        console.log(user)
        setUserInfo(user)
        window.alert('')
    }
    const dateFormat='YYYYMMDD'
    return (
        <Layout headerName='' className='addDeal' back={true}>
            <Wrapper>
                <div className="imgWrapper">
                    <img src={avatarSmall} alt=""/>
                </div>
                <form>
                    <div className="item">
                        <div className="title"></div>
                        <Input value={user.userName} placeHolder='' onBlur={userName=>onChange({userName})}/>
                    </div>
                    <div className="item">
                        <div className="title"></div>
                        <div className="sex" onClick={showTip}>{hashMap[user.sex]}</div>
                    </div>
                    <div className="item">
                        <div className="title"></div>
                        {/*@ts-ignore*/}
                        <DatePicker bordered={false} onChange={onChooseDate}
                                    defaultValue={moment(user.birth===''?'1997-01-23':user.birth,dateFormat)}
                        />
                    </div>
                    <div className="item">
                        <div className="title"></div>
                        <Input value={user.intro} placeHolder='' onBlur={intro => onChange({intro})}/>
                    </div>
                    <div className="item">
                        <div className="title"></div>
                        <Input value={user.city} placeHolder='' onBlur={city => onChange({city})}/>
                    </div>
                    <div className="item">
                        <div className="title"></div>
                        <Input value={user.major} placeHolder='' onBlur={major => onChange({major})}/>
                    </div>
                    <SaveButton buttonName='' onClick={submit}/>
                </form>
                <Tip titleName=''
                     onChange={visible=>ToggleVisible(visible)}
                     visible={tipVisible}
                >
                    <ul className='sexList'>
                        {/*@ts-ignore*/}
                        <li onClick={()=>{onChange({sex:'male'})}}></li>
                        {/*@ts-ignore*/}
                        <li onClick={()=>{onChange({sex:'female'})}}></li>
                    </ul>
                </Tip>
            </Wrapper>
        </Layout>
    )
}
export default UserInfo