import React, {useState} from 'react';
import Layout from '../components/Layout';
import Tags from './AddDeal/Tags';
import InputWrapper from './AddDeal/InputWrapper';
import Cate from './AddDeal/Cate';
import AddAccounts from './AddDeal/AddAccounts';
import AddDate from './AddDeal/AddDate';
import AddRemark from './AddDeal/AddRemark';
import SaveButton from "../components/SaveButton";
import useRecords, {newRecordItem} from '../hooks/useRecords';
import useAccounts from '../hooks/useAccounts';
import useCates from '../hooks/useCates';
import {Id} from '../lib/Id';

type Category = '-' | '+'

const defaultFormData = {
  cateIds: [] as number[],
  remark: '',
  expenseType: '-' as Category,
  amount: '',
  accountName:'',
  createdAt:new Date().toISOString()
} as newRecordItem
const AddDeal: React.FC = () => {
  const [selected,setSelected]=useState(defaultFormData)
  const {updateAccount}=useAccounts()
  const {updateCate}=useCates()
  const {addRecord}=useRecords()
  const onChange = (obj: Partial<typeof selected>) => {
    setSelected({...selected, ...obj});
  };
  const submit = () => {
    updateAccount(selected.accountName,Number(selected.amount),selected.expenseType)
    updateCate({id:(new Id().value),...selected})
    if (addRecord(selected)) {
      alert('');
    }else {
      alert('')
    }
    setSelected(defaultFormData);
  };
  return (
    <Layout
      headerName=''
      back={true}
      navVisible={false}
      className='addDeal'
    >
      <Tags value={selected.expenseType}
    onChange={expenseType => onChange({expenseType})}
    />
      <InputWrapper value={selected.amount} onChange={amount => onChange({amount})}/>
      <Cate value={selected.cateIds}
    type={selected.expenseType}
    onChange={cateIds => onChange({cateIds})}
    />
      <AddAccounts value={selected.accountName}
    onChange={accountName => onChange({accountName})}
    />
      <AddDate value={selected.createdAt} onHandle={createdAt => onChange({createdAt})}/>
      <AddRemark value={selected.remark} onChange={remark => onChange({remark})}/>
      <SaveButton buttonName='' onClick={submit}/>
    </Layout>
  );
};

export default AddDeal;
