import React from 'react';
import Layout from '../components/Layout';
import Card from '../components/Card';
import styled from 'styled-components';
import DataList from './Home/DataList';
import AccountData from './Home/AccountData';
import DealData from './Home/DealData';
import Budget from './Home/Budget';
import AddButton from '../components/AddButton';
import useAccounts from '../hooks/useAccounts';
import useRecords from '../hooks/useRecords';
import {useHistory} from 'react-router-dom';
import useCardVisible from "../hooks/useCardVisible";

const Button = styled.button`
  background-color: #fff;
  border: none;
  outline: none;
  margin-top: 14px;
  color: #51A1EC;
`
export const Home: React.FC = () => {
    const {getHomeData} = useCardVisible()
    const {accountList} = useAccounts()
    const {calPayOrIn, records} = useRecords()
    const paymentAmount = calPayOrIn('-')
    const incomeAmount = calPayOrIn('+')
    const history = useHistory()
    const DataListValue = {
        income: incomeAmount,
        payment: paymentAmount
    }
    const hash = {
        '': <DataList value={DataListValue}>{}</DataList>,
        '': <AccountData accountList={accountList}>{}</AccountData>,
        '': <div>
            <DealData records={records}>{}</DealData>
            <Button onClick={() => {
                history.push('/home/allDeal')
            }}></Button>
        </div>,
        '': <Budget>{}</Budget>
    }
    return (
        <Layout headerName='' navVisible={true}>
            {
                getHomeData().map(data => {
                    if (data.visible) {
                        return <Card key={data.subTitle} title={data.subTitle}>
                            {/*@ts-ignore*/}
                            {hash[data.subTitle]}
                        </Card>
                    }else {
                        return ''
                    }
                })
            }
            <AddButton routeName='/home/addDeal'>{}</AddButton>
        </Layout>
    );
};
