import React, {useState, useEffect} from 'react';
import Layout from '../components/Layout';
import useRecords from '../hooks/useRecords';
import useCharts from '../hooks/useCharts';
import StatisticChart from './Statistics/StatisticChart';
import useCardVisible from "../hooks/useCardVisible";

export const Statistics: React.FC = () => {
  //  
    const {getStatisticData}=useCardVisible()
  const {getCateChartData,getLineChartData} = useCharts();
  //option
  const [CateOptionOut, setCateOptionOut] = useState({});
  const [CateOptionIn, setCateOptionIn] = useState({});
  const [DailyTrendOptionOut, setDailyChartOut] = useState({});
  const [DailyTrendOptionIn, setDailyChartIn] = useState({});
  const [MonthlyOptionOut,setMonthlyOptionOut]=useState({})
  const [MonthlyOptionIn,setMonthlyOptionIn]=useState({})
  const {calPayOrIn} = useRecords();
  const totalPayment = calPayOrIn('-');
  const totalIncome = calPayOrIn('+');
  const [CateChartSwitch, setCateChart] = useState('');
  const [DailyChartSwitch, setDailyChart] = useState('');
  const [MonthlyChartSwitch, setMonthlyChart] = useState('');
  const hashMap={
      '':<StatisticChart totalPayment={totalPayment}
                             key={1}
                             totalIncome={totalIncome}
                             OptionIn={CateOptionIn}
                             OptionOut={CateOptionOut}
                             titleName=''
                             SwitchName={CateChartSwitch}
                             onChange={SwitchName => {setCateChart(SwitchName);}}
      >{}</StatisticChart>,
      '':<StatisticChart totalPayment={totalPayment}
                            totalIncome={totalIncome}
                            key={2}
                            OptionIn={DailyTrendOptionIn}
                            OptionOut={DailyTrendOptionOut}
                            titleName=''
                            SwitchName={DailyChartSwitch}
                            onChange={SwitchName => {setDailyChart(SwitchName);}}
      >{}</StatisticChart>,
      '':<StatisticChart totalPayment={totalPayment}
                             totalIncome={totalIncome}
                             key={3}
                             OptionIn={MonthlyOptionIn}
                             OptionOut={MonthlyOptionOut}
                             titleName=''
                             SwitchName={MonthlyChartSwitch}
                             onChange={SwitchName => {setMonthlyChart(SwitchName);}}
      >{}</StatisticChart>
  }
  useEffect(() => {
    setCateOptionOut(getCateChartData('-'));
    setCateOptionIn(getCateChartData('+'));

    setDailyChartOut(getLineChartData('-','day'));
    setDailyChartIn(getLineChartData('+','day'));

    setMonthlyOptionOut(getLineChartData('-','month'))
    setMonthlyOptionIn(getLineChartData('+','month'))
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  return (
    <Layout headerName='' navVisible={true}>
        {getStatisticData().map(data=>{
            if(data.visible){
                // @ts-ignore
                return hashMap[data.subTitle]
            }else {
                return ''
            }
        })}
    </Layout>
  );
};
