import styled from 'styled-components';
import React from 'react';

const Wrapper=styled.button`
  width: 80%;
  height: 35px;
  font-size: 16px;
  color: #fff;
  background-color:#EEA436;
  border: none;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
`

type Prop={
    buttonName:string,
    onClick?:()=>void
}

const SaveButton:React.FC<Prop> =(props)=>{
    return (
        <Wrapper onClick={props.onClick}>{props.buttonName}</Wrapper>
    )
}

export default SaveButton
