import {useState, useEffect} from 'react';
import {useUpdate} from "./useUpdate";

export type User={
    userName: string,
    sex: 'male'|'female',
    birth:string,
    intro:string,
    city:string,
    major:string
}
const defaultUserInfo={
    userName: '',
    sex: 'male',
    birth:'1997-02-23',
    intro:'',
    city:'',
    major:''
} as User
const useUserInfo = () => {
    const [userInfo, setUserInfo] = useState<User>(defaultUserInfo)
    useEffect(()=>{
        let localInfo=JSON.parse(window.localStorage.getItem('userInfo')||'{}')
        if(Object.keys(localInfo).length===0){
            localInfo=defaultUserInfo
            setUserInfo(localInfo)
            window.localStorage.setItem('userInfo', JSON.stringify(localInfo));
        }
        setUserInfo(localInfo)
    },[])
    useUpdate(() => {
        window.localStorage.setItem('userInfo', JSON.stringify(userInfo));
    }, [userInfo]);

    return {
        userInfo,
        setUserInfo
    }
}
export default useUserInfo