import React from 'react';
import styled from 'styled-components';
import {useHistory} from 'react-router-dom';
import Icon from './Icon';
const HeaderWrapper=styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 16px;
  background-color:#0AC775;
  padding:10px 18px;
  .back{
    width: 22px;
    height: 22px;
    margin-right: 10px;
  }
  >h3{
    flex-grow: 1;
    color: #fff;
    margin-bottom: 0;
  }
  .icon{
    width: 22px;
    height: 22px;
    fill: #fff!important;
  }
`
type Props={
  headerName: string,
  back?:boolean,
  IconName?: string
}& React.ScriptHTMLAttributes<HTMLDivElement>
const Header:React.FC<Props>=(props)=>{
  const history=useHistory()
  const goBack=()=>{
    history.goBack()
  }
  return (
    <HeaderWrapper>
      {props.back?<div className='back' onClick={goBack}><Icon name='arrowLeft' fill='#fff'></Icon></div>:''}
      <h3>{props.headerName}</h3>
      {props.IconName?<Icon name={props.IconName}></Icon>:''}
    </HeaderWrapper>
  )
}

export default Header
