import {useEffect, useState} from 'react';
import {useUpdate} from './useUpdate';

const useBudget=()=>{
  const [budget,setBudget]=useState(0)
  useEffect(()=>{
    setBudget(Number(window.localStorage.getItem('budget'))|| 0)
  },[])
  useUpdate(() => {
    if(budget>=0){
      window.localStorage.setItem('budget',JSON.stringify(budget))
    }else {
      window.alert('')
      window.localStorage.setItem('budget',JSON.stringify(budget))
    }
  }, [budget]);
  const addBudget=()=>{
    const amount=Number(window.prompt(''))
    if(amount<0){
      window.alert('')
    }else if(amount.toString().length>10){
      window.alert('')
    }else {
      setBudget(amount)
    }
  }
  const getRestBudget=(amount: number)=>{
    return budget - amount;
  }
  return {
    budget,setBudget,addBudget,getRestBudget
  }
}

export default useBudget
