import styled from 'styled-components';
import React, {useState} from 'react';
import {expenseType} from '../../hooks/useCharts';

const Wrapper = styled.ul`
  $white:#fff;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  max-width: 500px;
  position: absolute;
  top: 42px;
  left: 0;
  li{
    flex-grow: 1;
    font-size: 16px;
    color: #ddd;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:#0AC775;
    border: none;
    >span{
      display: inline-block;
      width: 30%;
      text-align: center;
      padding: 10px 0;
      &.active{
        color: #fff;
      }
    }
  }
`;

type Prop={
  value:expenseType,
  onChange:(expenseType: expenseType)=>void
}

const Tags:React.FC<Prop> =(props)=>{
  const categoryMap={'-':'','+':''}
  type Keys=keyof typeof categoryMap
  const [categoryList]=useState<(Keys)[]>(['-','+'])
  const category=props.value
  return (
    <Wrapper className='tags'>
      {
        categoryList.map(c=>
          <li key={c}
              onClick={()=>{props.onChange(c)}}
        ><span className={category === c ? 'active' : ''} >{categoryMap[c]}</span></li>)
      }
    </Wrapper>
  )
}

export default Tags
