import React, {useState} from 'react';
import styled from 'styled-components';
import Tip from '../../components/Tip';
import useAccounts from '../../hooks/useAccounts';

const Wrapper = styled.div`
    margin: 20px 40px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
    .subTitle{
     color: #AF9999;
     font-size: 8px;
    }
    .info{
      font-size: 12px;
      color: #828482;
    }
`;
const AccountList = styled.div`
  ul{
    display: flex;
    flex-direction: column;
    li{
      padding: 20px;
      font-size: 14px;
      border-bottom: 1px solid #ddd;
      display: flex;
      justify-content: space-between;
    }
  }
`;

type Prop= {
  value: string,
  onChange: (accountName: string) =>void
}
const AddAccounts: React.FC<Prop> = (props) => {
  const [TipVisible, setTipVisible] = useState(false);
  const {accountList,getAccountAmount}=useAccounts()
  const onChange = (visible: boolean) => {
    const newVisible = !visible;
    setTipVisible(newVisible);
    return newVisible;
  };
  const onClick=(accountName: string)=>{
    if(accountName){
      props.onChange(accountName)
    }
  }
  return (
    <Wrapper>
      <div className="subTitle"></div>
      <div className="info" onClick={() => {onChange(TipVisible);}}>{props.value.length?props.value+`(${getAccountAmount(props.value)})`:''}</div>
      <Tip visible={TipVisible}
           titleName=''
           onChange={TipVisible => {onChange(TipVisible);}}
      >
        <AccountList>
          <ul>
            {accountList.length?accountList.map(account=>{
              return <li key={account.id} onClick={()=>onClick(account.accountName)}>
                <span>{account.accountName}</span>
                <span>{getAccountAmount(account.accountName)}</span>
              </li>
            }):<li><span></span></li>}
          </ul>
        </AccountList>
      </Tip>
    </Wrapper>
  );
};
export default AddAccounts;
